$('a#js-offers-link').on('click', function (e) {
    e.preventDefault();
    var $offersList = $(this).prev('.offers__list');
    $offersList.toggleClass('offers__list--active');
    if ($offersList.hasClass('offers__list--active')) {
        $(this).text('Скрыть');
    } else {
        $(this).text('Показать еще');
    }
});